const changeNetwork = async () => {
  try {
    if (!window.ethereum) throw new Error("No crypto wallet found");
    await window.ethereum.request({
      method: "wallet_switchEthereumChain",
          params: [{ chainId: "0x13881" }]
    });
  } catch (err) {
    console.log(err.message);
  }
};

var acc, contract,tx, url, provider, signer, admin;
function App() {
  const [ethAddress, setEthAddress] = useState()
  const [cFlag, setCFlag] = useState()

  useEffect(() => {
    ethEnabled()
    changeNetwork()
  })


  async function ethEnabled () {
    url = "https://polygon-mumbai.g.alchemy.com/v2/dUO0jXzmO9_IihkDUcpOPu6VDUUWzmAf";

    provider = new ethers.providers.JsonRpcProvider(url);
    
    if(typeof window.ethereum!=='undefined'){
      const web3 = new ethers.providers.Web3Provider(window.ethereum);
      window.ethereum.enable()

      acc = await web3.listAccounts()
      setEthAddress(acc[0])

      signer = await web3.getSigner(ethAddress);
    }else {
      alert("Login to metamask")
    }
    
    try {
      contract = new ethers.Contract('0xAa2d99c81ADa3B7037De78F5c4c0f2B6601CE7Cf', abi, signer)
      console.log(contract)
    }
    catch{
    }
  }

  async function mintNft () {

    if(typeof contract !== 'undefined'){

      await (contract.safeMint(ethAddress.toString())).then(result => {alert(result.hash.toString())})
      
    }
  }
   

  return (
    <div className="App">
      <button onClick={mintNft}>Click to mint NFT</button>
    </div>
  );
}

export default App;
